// Khai css
.feedback {
    margin-top: $mg-2;
    background-color: #80808012;

    text-align: center;
    .feedback-content {
        // display: flex;
        // justify-content: center;
        .feedback-items {
            background-color: #fff;
            padding: 30px;

            background-color: #fff;

            width: 535px;
            height: 350px;
            img {
                border-radius: 50%;

                margin: 0 auto;
                width: 100px;
                height: 100px;
            }
            i {
                color: rgb(255, 179, 0);
            }
        }
    }
}
.headerBorderTop {
    margin-top: 50px;
    width: 100%;
    height: 0.5rem;
    background: linear-gradient(
        90deg,
        rgb(100, 91, 83) 0%,
        rgb(235, 82, 82) 18.23%,
        rgb(247, 143, 47) 34.37%,
        rgb(244, 193, 81) 48.96%,
        rgb(82, 187, 118) 66.15%,
        rgb(38, 165, 215) 82.29%,
        rgb(224, 105, 183) 100%
    );
}
